import React, { useState, useEffect } from "react";
import Formulario from "./components/Formulario";
import Montos from "./components/Montos";
import Pregunta from "./components/Pregunta";
import Tabla from "./components/Tabla";

const App = () => {
  const [Question, setQuestion] = useState(true);
  const [presupuesto, setPresupuesto] = useState(0);
  const [restante, setRestante] = useState(0);

  const [gasto, setGasto] = useState({});
  const [gastos, setGastos] = useState([]);
  const [crearGasto, setCrearGasto] = useState(false);
  const [devolverGasto, setDevolverGasto] = useState(false);
  const [rest, setRest] = useState(0);

  

  //localStorage
  let almacenamiento = JSON.parse(localStorage.getItem("almacenamiento"));
  if (!almacenamiento) {
    almacenamiento = {};
  }

  //UseEffect
  useEffect(() => {

    if (crearGasto) {
      setGastos([...gastos, gasto]);
      let resto = restante - gasto.calculo;
      setRestante(resto);
      setCrearGasto(false);
    }

    if (devolverGasto) {
      let resto = restante + rest;
      setRestante(resto);
      setDevolverGasto(false);
    }
  }, [
    setGastos,
    gastos,
    gasto,
    setCrearGasto,
    setRestante,
    crearGasto,
    restante,
    devolverGasto,
    rest,
    Question,
    presupuesto,
  ]);

  return (
    <div className="container">
      <h1 className="text-center mb-3">Presupuesto SuperMercadoAPP</h1>
      {Question ? (
        <div className="row justify-content-center">
          <Pregunta
            guardarPresupuesto={setPresupuesto}
            guardarRestante={setRestante}
            setQuestion={setQuestion}
          />
        </div>
      ) : (
        <div className="row d-flex align-content-center flex-wrap">
          <div className="col-md-6">
            <Formulario
              setGasto={setGasto}
              setGastos={setGastos}
              setCrearGasto={setCrearGasto}
            />
            <Montos presupuesto={presupuesto} restante={restante} />
          </div>
          <div className="col-md-6 mt-5">
            <Tabla
              gastos={gastos}
              setGastos={setGastos}
              setDevolverGasto={setDevolverGasto}
              setRest={setRest}
            />
          </div>
        </div>
      )}
    </div>
  );
};

export default App;
