import React, { useState } from "react";
import Error from "./Error";
import market from "../images/market.svg";

const Pregunta = ({ guardarPresupuesto, guardarRestante, setQuestion }) => {
  const [cantidad, setCantidad] = useState(0);
  const [error, setError] = useState(false);

  const definirPresupuesto = (e) => {
    setCantidad(parseInt(e.target.value));
  };

  const agregarPresupuesto = (e) => {
    e.preventDefault();
    //validar
    if (cantidad < 1|| cantidad === isNaN) {
      setError(true);
      return;
    }
    //si pasa la validacion
    setError(false);
    setQuestion(false);
    guardarPresupuesto(cantidad);
    guardarRestante(cantidad);
    
  };

  return (
    <div className="col-12 col-md-6 d-flex justify-content-center">
      <form className="form-group" onSubmit={agregarPresupuesto}>
      <div className='row d-flex justify-content-center'>
        <img className="imagen text-center" src={market} alt=""/>
        </div>
        <h3 className="text-center mt-4">¿Cuanto es tu monto disponible?</h3>
        {error ? <Error mensaje="debe ingresar un monto valido" /> : null}
        <input
          onChange={definirPresupuesto}
          type="number"
          className="form-control mt-3"
          placeholder=""
        />
        <input
          type="submit"
          className="btn btn-primary mt-3 text-center btn-block"
          value="Definir Presupuesto"
        />
      </form>
    </div>
  );
};

export default Pregunta;
