import React from "react";

const Tabla = ({ gastos ,setGastos, setDevolverGasto,setRest}) => {

    const eliminar = (g) =>{
        const resultado = gastos.filter(gasto => gasto.id !== g.id);

        setGastos(resultado);
        setRest(g.calculo);
        setDevolverGasto(true);
    }

  return (
    <table className="table">
      <thead>
        <tr>
          <th scope="col">Producto</th>
          <th scope="col">Precio</th>
          <th scope="col">Cant</th>
          <th scope="col">Total</th>
        </tr>
      </thead>
      <tbody>
        {gastos.map((gasto) => (
          <tr key={gasto.id}>
            <td>{gasto.nomPro}</td>
            <td>{gasto.precioPro}</td>
            <td>{gasto.cantPro}</td>
            <td>{gasto.calculo}</td>
            <td>
              {
                <button
                onClick={() => eliminar(gasto)}
                className="btn btn-danger"
              >
                X
              </button>}
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};

export default Tabla;
