import React, { useState } from "react";
import Error from "./Error";
import shortid from "shortid";

const Formulario = ({ setGasto, setCrearGasto}) => {
  const [nomPro, setNomPro] = useState("");
  const [precioPro, setPrecioPro] = useState("");
  const [cantPro, setCantPro] = useState("");
  const [calculo, setCalculo] = useState(0);
  const [error, setError] = useState(false);

  const calcular = (e) => {
    e.preventDefault();
    //parsear los input
    setPrecioPro(parseInt(precioPro));
    setCantPro(parseInt(cantPro));

    //validar
    if (isNaN(precioPro) || isNaN(cantPro) || precioPro < 1 || cantPro < 1) {
      setError(true);
      return;
    }

    //definir el calculo y cambiar posible estado de error
    let total = precioPro * cantPro;
    parseInt(total);
    setCalculo(total);
    setError(false);
  };

  const agregarCarro = (e) => {
    e.preventDefault();

    const gasto = {
      id: shortid.generate(),
      nomPro,
      precioPro,
      cantPro,
      calculo,
    };
    setGasto(gasto);
    setCrearGasto(true);
    

    //resetear form
    setNomPro('');
    setPrecioPro('');
    setCantPro(1);
    setCalculo(0);
  };

  return (
    <form className="form-group mt-3" onSubmit={agregarCarro}>
      {error ? <Error mensaje="Error al ingresar los datos" /> : null}
      <label>Nombre Producto</label>
      <input
        type="text"
        className="form-control"
        placeholder="Ej. Pan"
        value={nomPro}
        onChange={(e) => setNomPro(e.target.value)}
      />

      <div className="row">
        <div className="col mt-3">
          <label>Precio del producto</label>
          <input
            type="number"
            className="form-control"
            placeholder="Ej. 990"
            value={precioPro}
            onChange={(e) => setPrecioPro(e.target.value)}
          />
        </div>
        <div className="col mt-3">
          <label>Cant producto</label>
          <input
            type="number"
            className="form-control"
            placeholder="Ej. 1"
            value={cantPro}
            onChange={(e) => setCantPro(e.target.value)}
          />
        </div>
      </div>
      <label className="mt-3">Precio Final</label>
      <div className="row">
        <div className="col-6">
          <input
            type="number"
            className="form-control"
            placeholder="calculo"
            disabled
            value={calculo}
            onChange={(e) => setCalculo(e.target.value)}
          />
        </div>
        <div className="col-6">
          <button
            onClick={(e) => calcular(e)}
            className="btn btn-primary btn-block mb-2"
          >
            Calcular
          </button>
        </div>
      </div>
      <button type="submit" className="btn btn-danger btn-block mt-4">
        Agregar al Carro
      </button>
    </form>
  );
};

export default Formulario;
